@charset "utf-8";

:root {
  --app-dark-bg: #1a1d28;
  --app-green: #33ab4e;
  --app-purple: #833cff;
  --app-orange1: #FF7B02;
  --app-dark-blue: #1a2649;
  --app-lighter-blue: #506290;
  --app-medium-blue: #283862;
  --app-grey: #b4b3bd;
  --app-red: #ff3c57;
  --app-red-hover: #ff2846;
}

body {
  font-family: 'Nunito Sans', sans-serif;
  background-color: #1a1924;
}

.container {
  width: min(100% - 30px, 1200px);
  margin-inline: auto;
}

main {
  height: calc(100vh - 60px);
}


/*-----------Header------------*/
header {
  background-color: #26293034;
  border-bottom: 1px solid #ffffff48;
}

.hflex {
  display: flex;
  justify-content: space-between;
  height: 60px;
  line-height: 60px;
}

h1 {
  color: var(--app-orange1);
}

.hflex nav ul {
  display: flex;
  justify-content: space-between;
  color: #fff;
}

.hflex nav ul li {
  font-weight: 400;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  list-style: none;
  display: block;
  padding: 0 10px;
}


.hflex nav ul li:hover {
  background-color: var(--app-orange1);
}


/*-----------------------------------------------------*/
/* Buttons                                             */
/*-----------------------------------------------------*/

.buttons {
  margin: 50px 0;
  text-align: right;
}

.sort {
  width: 100px;
  height: 35px;
  border-radius: 5px;
  border: 2px solid var(--app-orange1);
  color: var(--app-orange1);
  font-size: 18px;
  font-weight: 600;
  margin-right: 15px;
}

.add {
  width: 100px;
  height: 35px;
  background-color: #09f;
  border-radius: 5px;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  border: 2px solid #fff;
}

/*-----------------------------------------------------*/
/* Grid                                                */
/*-----------------------------------------------------*/

.display {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  padding: 10px;
  gap: 20px;
}

.post {
  height: 300px;
  width: 200px;
  border-radius: 5px;
  background-color: #1a1924;
  margin-inline: auto;
  margin-bottom: 20px;
  cursor: pointer;
}

.post img {
  border-radius: 5px;
  height: 245px;
}

.post h3 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #fff;
  margin-top: 3px;
}

.info {
  display: flex;
  justify-content: left;
}

.info p {
  color: rgb(255 255 255 / 0.4);

}

.info p:first-child {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-right: 5px;
}

.info p:nth-child(2) {
  margin-top: -1px;
  color: #ffffffa6;
}

.info p:nth-child(3) {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-left: 5px;
}


/*-----------------------------------------------------*/
/* Footer                                              */
/*-----------------------------------------------------*/

footer {
  margin-top: 50px;
  background-color: #26293034;
  border-top: 1px solid #ffffff48;
  height: 110px;
}

.fflex {
  text-align: center;
  color: #fff;
}

.fflex h1 {
  margin-top: 3px;
  font-size: 24px;
  color: var(--app-orange1);
}


.fflex .copy {
  margin-bottom: 15px;
  color: #fff;
}

.fflex .policy {
  display: flex;
  justify-content: center;
}

.fflex .policy p a {
  color: #fff;
  padding: 0 10px;
  text-decoration: none;
  transition: all 0.5s ease-in-out;
}

.fflex .policy p a:hover {
  color: var(--app-orange1);
}

